.App {
  text-align: center;
}

.App-intro {
  margin-left: auto;
  margin-right: auto;
  max-width: 400px;
  text-align: left;
  margin-top: 15px;
  margin-bottom: 35px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-settings-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.App-settings-form-item {
  margin-bottom: 10px;
}

.App-settings-form-item > label {
  display: inline-block;
  min-width: 100px;
  text-align: left;
}

.App-settings-form-item > input,
.App-settings-form-item > select {
  min-width: 250px;
}




